import {createBrowserRouter} from 'react-router-dom';

export const MAP_ROUTE = [
  {path: '/', title: '   ', element: (<MainPage/>), auth: ['ALL']},
  {path: '/project', title: ' ', element: (<MainProjectPage/>), auth: ['ALL']},
  {path: '/study', title: ' ', element: (<MainStudyPage/>), auth: ['ALL']},
  {path: '/mentee', title: '', element: (<MainMenteePage/>), auth: ['ALL']},
  {path: '/mentor', title: '', element: (<MainMentorPage/>), auth: ['ALL']},
  {path: '/feed', title: '', element: (<MainFeedPage/>), auth: ['ALL']},
  {path: '/jobs', title: '', element: (<MainJobPostingPage/>), auth: ['ALL']},

  {path: '/feed/:feedId', title: ' ', element: (<FeedDetailPage/>), auth: ['LOGIN']},
  {path: '/create/feed', title: ' ', element: (<EditFeedPage/>), auth: ['LOGIN']},
  {path: '/update/feed/:feedId', title: ' ', element: (<EditFeedPage/>), auth: ['LOGIN']},

  {path: '/job/:id', title: ' ', element: (<JobDetailPage/>), auth: ['ALL']},
  {path: '/create/job', title: ' ', element: (<JobPostingEditPage/>), auth: ['ADMIN']},

  {path: '/login/token', title: '', element: (<LoginToken/>), auth: ['ALL']},
  {path: '/logout/token', title: '', element: (<LogoutToken/>), auth: ['ALL']},
  {path: '/join', title: '', element: (<SignUpTerms/>), auth: ['ALL']},
  {path: '/join/additional-info', title: ' ', element: (<UserAdditionalInfo/>), auth: ['ALL']},

  {path: '/mypage/group', title: ' ', element: (<MyGroup/>), auth: ['LOGIN']},
  {path: '/mypage/like', title: '  ', element: (<MyLike/>), auth: ['LOGIN']},
  {path: '/mypage/inquiry', title: ' ', element: (<MyInquiryPage/>), auth: ['LOGIN']},
  {path: '/mypage/mentor', title: ' ', element: (<MyMentoring/>), auth: ['LOGIN']},

  {path: '/team/:teamId', title: ' ', element: (<ProjectDetailPage/>), auth: ['ALL']},
  {path: '/create/team', title: ' ', element: (<EditProjectInfoPage/>), auth: ['LOGIN']},
  {path: '/update/team/:teamId', title: ' ', element: (<EditProjectInfoPage/>), auth: ['LOGIN']},

  {path: '/mypage/profile', title: ' ', element: (<UserDetailPage/>), auth: ['LOGIN']},
  {path: '/profile/:userId', title: ' ', element: (<UserDetailPage/>), auth: ['ALL']},
  {path: '/update/profile', title: ' ', element: (<EditProfileInfoPage/>), auth: ['LOGIN']},
  {path: '/profile/settings', title: ' ', element: (<ProfileSetting/>), auth: ['LOGIN']},

  {path: '/auth/mentor', title: ' ', element: (<MentorAuthPage/>), auth: ['LOGIN']},
  {path: '/auth/enterprise', title: ' ', element: (<CompanyAuthPage/>), auth: ['LOGIN']},
  {path: '/create/mentoring', title: ' ', element: (<EditMentoringPage/>), auth: ['MENTOR', 'ADMIN']},
  {path: '/update/mentoring/:mentoringId', title: ' ', element: (<EditMentoringPage/>), auth: ['MENTOR', 'ADMIN']},

  {path: '/chat', title: '', element: (<ChatPage/>), auth: ['LOGIN']},

  {path: '/admin', title: ' ', element: (<AdminPage/>), auth: ['ADMIN']},
  {path: '/admin/auth/mentor', title: ' ', element: (<MentorVerifyPage/>), auth: ['ADMIN']},
  {path: '/admin/bug', title: ' ', element: (<BugReportPage/>), auth: ['ADMIN']},
  {path: '/admin/test/stack', title: '', element: (<CreateStackImagePage/>), auth: ['ADMIN']},
  {path: '/admin/test/chat', title: '', element: (<TestChattingPage/>), auth: ['ADMIN']},

  {path: '/announcement', title: '', element: (<Announcement/>), auth: ['ALL']},
  {path: '/inquiry/personal', title: '   ', element: (<Inquiry/>), auth: ['ALL']},
  {path: '/faq', title: '  ', element: (<FAQ/>), auth: ['ALL']},
  {path: '/terms/info', title: '', element: (<TermsOfInfo/>), auth: ['ALL']},
  {path: '/terms/service', title: ' ', element: (<TermsOfService/>), auth: ['ALL']},

  {path: '/not-found', title: '   ', element: (<Page404/>), auth: ['ALL']},
  {path: '*', title: '   ', element: (<Page404/>), auth: ['ALL']},
];

export const BROWSER_ROUTER = createBrowserRouter(
  MAP_ROUTE.map((route) => ({
    path: route.path,
    element: route.element,
  }))
);
